import React, { useState } from "react";
import styled from "styled-components";

const StyledToggleButton = styled.button`
    width: 100%;
    padding: 10px;
    font-size: 16px;
    font-weight: bold;
    cursor: pointer;
    border: 1px solid ${({ borderColor }) => borderColor || "rgb(15, 138, 81)"};
    border-radius: 8px;
    transition: background-color 0.2s, color 0.2s;

    color: ${({ isActive, hovertextColor, textColor }) =>
        isActive
            ? "rgb(255, 255, 255)"  /*     */
            : (textColor || "rgb(15, 138, 81)")}; /*      */

    background-color: ${({ isActive, hoverbgColor, bgColor }) =>
        isActive
            ? (hoverbgColor || "rgb(15, 138, 81)")  /*    */
            : (bgColor || "rgb(255, 255, 255)")};  /*    */

    &:hover {
        color: ${({ isActive, textColor, hovertextColor }) =>
        isActive
            ? (hovertextColor || "rgb(15, 138, 81)")  /*     */
            : "rgb(255, 255, 255)"};  /*     */

        background-color: ${({ isActive, bgColor, hoverbgColor }) =>
        isActive
            ? (bgColor || "rgb(255, 255, 255)")  /*     */
            : (hoverbgColor || "rgb(15, 138, 81)")};  /*     */
    }
`;
//border, text, hoverbg, hovertext  
export default function ToggleButton(props) {
    const { title, onClick, borderColor, textColor, bgColor, hovertextColor, hoverbgColor } = props;
    const [isActive, setIsActive] = useState(false);

    const handleClick = () => {
        setIsActive(!isActive);
        if (onClick) onClick();
    };

    return (
        <StyledToggleButton
            isActive={isActive}
            onClick={handleClick}
            borderColor={borderColor}
            textColor={textColor}
            bgColor={bgColor}
            hovertextColor={hovertextColor}
            hoverbgColor={hoverbgColor}
        >
            {title}
        </StyledToggleButton>
    );
}
