import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../contextAPI/AuthContext";
import styled from "styled-components";
import Input_Email from "./Inputs/Input_Email";
import Input_Password from "./Inputs/Input_Password";
import Input_PasswordCheck from "./Inputs/Input_PasswordCheck.jsx";
import Button from "./Inputs/Button";
import Input from "./Inputs/Input.jsx";
import { CONFIG } from "../config.ts"
const Wrapper = styled.div`
    width: 500px; /*  */
  height: 700px; /*  */
  display: flex; /*    flexbox */
  flex-direction : column; /* */
  justify-content: center; /*    */
  align-items: center; /*    */
  font-weight: bold; /*   */
  gap : 1px;
  color: #333; /*   */
`;
const StyledP = styled.p`
    font-size : 25px;
    font-weight : bold;
    font-family : 'Poppins', 'Montserrat', 'Noto Sans KR', sans-serif !important;
    margin-bottom : 40px;
    color : #fff;
    text-shadow : 8px 8px 8px rgb(41, 93, 161);
    bottom : 30px;
`;
const FormBoxContainer = styled.div`
    margin-bottom : 10px;
    width:80%;
    display : flex;
    flex-direction : column;
    justify-content : center;
    align-items : flex-start;
    position : relative;
`
const MainExplanationContainer = styled.div`
    display: flex;
    align-items: flex-start;
    gap: 5px;  /*     */
    margin-bottom: 4px;  /* Input  */
`;

const MainExplanationP = styled.p`
    display: flex;
    align-items: center;  /*    */
    font-size: 18px;
    color: #fff;
    text-shadow : 1px 1px 2px rgba(0, 0, 0, 0.8);
    margin-bottom: 1px;
`;

const SubExplanationP = styled.span`
    display: flex;
    align-items: center; /*      */
    line-height: 18px; /*         */
    font-size: 12px;
    font-weight : bold;
    color: rgb(0, 0, 0);
    text-shadow : 0.5px 0.5px 1px rgba(255, 255, 255, 0.8);
`;
export default function Standard_Register(props) {
    const navigate = useNavigate();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const [passwordCheck, setPasswordCheck] = useState('');
    const [name, setName] = useState('');
    const { fetchUserInfo } = useAuth();
    const handleRegister = async () => {
        if (email === "" || password === "" || passwordCheck === "" || name === "") {
            return alert("    !");
        }
        if (!(email.includes("@") && email.includes("."))) {
            return alert("   !");
        }
        try {
            if (password === passwordCheck) {
                const response = await fetch(CONFIG.BACK_SERVER + "/api/users/register", {
                    method: "POST",
                    headers: {
                        "Content-Type": "application/json",
                    },
                    credentials: "include",
                    body: JSON.stringify({ email, password, name })
                });

                const data = await response.json();

                if (response.ok) {
                    console.log('  : ', data.doc);
                    await fetchUserInfo();
                    navigate("/");
                } else {
                    alert("  : " + data.error);
                }
            } else {
                alert("    .")
            }
        } catch (error) {
            console.error("   ", error);
        }
    }
    return (
        <Wrapper>
            <StyledP>KS Page</StyledP>
            <FormBoxContainer>
                <MainExplanationContainer>
                    <MainExplanationP>*</MainExplanationP>
                    <SubExplanationP>( .)</SubExplanationP>
                </MainExplanationContainer>
                <Input_Email Email={email} onChange={(e) => setEmail(e.target.value)} />
            </FormBoxContainer>
            <FormBoxContainer>
                <MainExplanationContainer >
                    <MainExplanationP>*</MainExplanationP>
                    <SubExplanationP>(  .)</SubExplanationP>
                </MainExplanationContainer>
                <Input_Password Password={password} onChange={(e) => setPassword(e.target.value)} />
            </FormBoxContainer>
            <FormBoxContainer>
                <MainExplanationContainer >
                    <MainExplanationP>* </MainExplanationP>
                </MainExplanationContainer>
                <Input_PasswordCheck PasswordCheck={passwordCheck} onChange={(e) => setPasswordCheck(e.target.value)} />
            </FormBoxContainer>
            <FormBoxContainer>
                <MainExplanationContainer>
                    <MainExplanationP>*</MainExplanationP>
                    <SubExplanationP>()</SubExplanationP>
                </MainExplanationContainer>
                <Input type={"text"} value={name} placeholder={"()"} onChange={(e) => setName(e.target.value)} />
            </FormBoxContainer>
            <FormBoxContainer>
                <Button title="" borderColor={"rgb(0, 123, 255)"} textColor={"rgb(0, 123, 255)"} hoverbgColor={"rgb(0, 123, 255)"} onClick={handleRegister} />
            </FormBoxContainer>
        </Wrapper>
    )
}