import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../contextAPI/AuthContext";
import styled from "styled-components";
import Input from "./Inputs/Input";
import TextInput from "./Inputs/TextInput";
import Button from "./Inputs/Button";
import ToggleButton from "./Inputs/ToggleButton.jsx";
import { CONFIG } from "../config.ts"
const Wrapper = styled.div`
    width : 1000px;
    height : 800px;
    display : flex;
    flex-direction : column;
    align-items : flex-start;
    gap : 8px;
`
const BackButtonContainer = styled.div`
    width : 10%;
    display : flex;
    flex-direction : column;
    align-items : flex-start;
    justify-content : center;
`
const InputContainer = styled.div`
    width : 100%;
    display : flex;
    flex-direction : column;
    align-items : flex-start;
    justify-content : center;
    gap : 8px;
`
const FinishedButtonContainer = styled.div`
    width: 20%;
    display: flex;
    flex-direction: flex-start;
    align-items: flex-end;
    justify-content: center;
    margin-left: auto;
    gap : 8px;
`
export default function Standard_PostWrite(props) {
    const navigate = useNavigate();
    const { fetchUserInfo, user } = useAuth();
    const [title, setTitle] = useState('');
    const [content, setContent] = useState('');
    const [anonymous, setAnonymous] = useState(false);
    const author = user.name;
    const handleUploadPost = async () => {
        try {
            const response = await fetch(CONFIG.BACK_SERVER + "/api/posts/upload", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                credentials: "include",
                body: JSON.stringify({ author, title, content, anonymous })
            })
            const data = await response.json();
            if (response.ok) {
                console.log('  : ', data.doc);
                await fetchUserInfo();
                navigate("/boardroom")
            } else {
                alert("   : " + data.error);
            }
        } catch (err) {
            console.error("   : " + err);
        }
    }
    const handleBack = async () => {
        await navigate("/boardroom")
    }
    const handleAnonymous = async () => {
        await setAnonymous(!anonymous);
    }
    return (
        <Wrapper>
            <BackButtonContainer>
                <Button title={""} borderColor={"rgb(0, 123,255)"} textColor={"rgb(0, 123,255)"} hoverbgColor={"rgb(0, 123,255)"} onClick={handleBack}></Button>
            </BackButtonContainer>
            <InputContainer>
                <Input type={"text"} value={title} onChange={(e) => { setTitle(e.target.value) }} placeholder={" ."} />
                <TextInput value={content} onChange={(e) => { setContent(e.target.value) }} placeholder={" ."} height={"600"} />
            </InputContainer>
            <FinishedButtonContainer>
                <ToggleButton title="" onClick={handleAnonymous} borderColor={"rgb(220, 53, 69)"} textColor={"rgb(220, 53, 69)"} hoverbgColor={"rgb(220, 53, 69)"} hovertextColor={"rgb(220, 53, 69)"} />
                <Button title={""} onClick={handleUploadPost} disabled={title.length === 0 || content.length === 0} />
            </FinishedButtonContainer>
        </Wrapper>
    )
}