import React, { useState } from "react";
import { useNavigate } from "react-router-dom";
import { useAuth } from "../contextAPI/AuthContext";
import styled from "styled-components";
import Input_Email from "./Inputs/Input_Email";
import Input_Password from "./Inputs/Input_Password";
import Button from "./Inputs/Button";
import { CONFIG } from "../config.ts"
const StyledP = styled.p`
    position : absolute;
    font-size : 25px;
    font-weight : bold;
    font-family : 'Poppins', 'Montserrat', 'Noto Sans KR', sans-serif !important;
    margin-bottom : 400px;
    color : #fff;
    text-shadow : 0 3px 5px rgb(41, 93, 161);
`;
const Wrapper = styled.div`
    width: 500px; /*  */
  height: 700px; /*  */
  background-color: rbba(0, 0, 0, 0.5);/*  */
  border: 0px solid rgba(0, 0, 0, 0.9); /*    */
  border-radius: 16px; /*   */
  box-shadow: 0 0px 0px rgb(255, 255, 255); /*  */
  display: flex; /*    flexbox */
  flex-direction : column; /* */
  justify-content: center; /*    */
  align-items: center; /*    */
  font-size: 18px; /*   */
  font-weight: bold; /*   */
  gap : 10px;
  position : relative;
  color: #333; /*   */
`;
const InputContainer = styled.div`
    width : 80%;
    display : flex;
    flex-direction : column;
    align-items : center;
    justify-content : center;
    gap : 8px;
`
const MainButtonContainer = styled.div`
    width : 80%;
    display : flex;
    flex-direction : column;
    align-items : center;
    justify-content : center;
`
const SubButtonContainer = styled.div`
    display: flex;
    justify-content: space-between; //     
    width: 80%;
    gap : 5px; //  
`;
export default function Standard_Login(props) {
    const navigate = useNavigate();
    const [email, setEmail] = useState('');
    const [password, setPassword] = useState('');
    const { fetchUserInfo } = useAuth();
    const handleLogin = async () => {
        try {
            const response = await fetch(CONFIG.BACK_SERVER + "/api/users/login", {
                method: "POST",
                headers: {
                    "Content-Type": "application/json",
                },
                credentials: "include", //   
                body: JSON.stringify({ email, password }),
            });

            const data = await response.json();

            if (response.ok) {
                console.log('  : ', data);
                await fetchUserInfo();
                navigate("/");
            } else {
                alert("  : " + data.message);
            }
        } catch (error) {
            console.error("   ", error);
        }
    }
    const handleRegisterroom = async () => {
        try {
            navigate("/registerroom");
        } catch (error) {
            console.error(error);
        }
    }
    return (
        <Wrapper>
            <StyledP></StyledP>
            <InputContainer>
                <Input_Email Email={email} onChange={(e) => setEmail(e.target.value)} />
                <Input_Password Password={password} onChange={(e) => setPassword(e.target.value)} />
            </InputContainer>
            <MainButtonContainer>
                <Button title="" onClick={handleLogin} bgColor="" hoverColor="" />
            </MainButtonContainer>
            <SubButtonContainer>
                <Button title="" borderColor={"rgb(78,78,78)"} textColor={"rgb(255, 255, 255)"} bgColor={"rgb(121, 121, 121)"} hovertextColor={"rgb(255, 255, 255)"} hoverbgColor={"rgb(78, 78, 78)"} onClick={handleRegisterroom} />
                <Button title=" " borderColor={"rgb(59,59,59)"} textColor={"rgb(255, 255, 255)"} bgColor={"rgb(0, 0, 0)"} hovertextColor={"rgb(255, 255, 255)"} hoverbgColor={"rgb(59, 59, 59)"} />
            </SubButtonContainer>
        </Wrapper>
    )
}